.home-page .about {
  padding: 30px 0;
  min-height: 100vh;
  background: #050505;
  font-family: 'FuturaBookC', sans-serif;

  &-title {
    margin-bottom: 30px;
  }

  &-link {
    display: none;
  }

  &-list {

    &-photo img {
      width: 100%;
    }

    &-item {
      color: #fff;
      border-radius: 20px;
      overflow: hidden;
      background: rgba(53, 53, 53, 0.63);
      margin-bottom: 20px;
    }

    &-data {
      padding: 17px;
    }

    &-date {
      font-size: 18px;
    }

    &-desc {
      font-size: 22px;
      margin: 15px 0 25px 0;
    }

    &-link {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 22px;

      img {
        margin-bottom: -5px;
        padding-right: 10px;
      }
    }

    a {
      display: inline-block;
      padding: 6px 35px;
      border: 1px solid #fff;
      border-radius: 20px;
      color: #fff;
      text-decoration: none;
    }

    &-likes {
      padding-right: 15px;
    }

  }

  @media only screen and (min-width: 768px) {
    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;

      &-item {
        width: 45%;
      }
    }
  }

  @media only screen and (min-width: 1024px) {
    background: url("../../../assets/images/about-bg.png"), #050505;
    background-size: cover;

    &-list {
      justify-content: space-between;

      &-item {
        width: 32%;
      }

      &-likes {
        padding-right: 25px;
      }

    }

    &-link {
      display: block;
      font-size: 20px;
      text-align: center;
      margin-top: 30px;

      a {
        color: #fff;
        text-decoration: none;
      }

      span {
        padding-right: 20px;
      }

      img {
        margin-bottom: -5px;
      }
    }

    a {
      padding: 6px 45px;
    }

  }

}
