.home-page .portfolio {
  background: url("../../../assets/images/mountain.png");
  background-size: cover;
  position: relative;
  font-family: 'FuturaBookC', sans-serif;

  &-container {
    max-width: 1440px;
    margin: 0 auto;
  }

  &-title{
    position: absolute;
    left: 50%;
    top: 30px;
    transform: translateX(-50%);
    z-index: 50;
    color: #d0cfcf;
    letter-spacing: 15px;
    text-transform: uppercase;
  }

  &-item {
    height: 100vh;
    display: flex;
    align-items: flex-end;
    padding: 40px;
    color: rgba(255, 255, 255, 0.6);


    span {
      font-size: 24px;
    }

    p {
      font-size: 72px;
    }

    a {
      display: inline-block;
      overflow: hidden;
      height: 0;
      width: 100%;
      text-align: right;
      margin-top: 50px;
      transition: 0.3s all linear;
    }

  }

  .swiper-button-prev,
  .swiper-button-next{
    z-index: 1000;
    color: #7B7B7B;
  }


  .swiper-slide-active .portfolio-item{
    color: #fff;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%);

    a {
      height: 50px;
    }
  }

  &-link{
    display: none;
  }


  @media only screen and (min-width: 1024px) {
    &-item {
      a {
        text-align: left;
      }
    }
  }

  @media only screen and (min-width: 1200px) {
    .swiper-slide-active .portfolio-item{
      color: rgba(255, 255, 255, 0.6);
      background: transparent;

      a {
        height: 0;
      }
    }

    &-item {
      padding: 60px;

      div{
        margin-bottom: 50px;
      }

      span {
        font-size: 24px;
      }

      p {
        font-size: 72px;
      }
    }

    .swiper-slide .portfolio-item:hover{
      color: #fff;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0) 100%);

      a {
       height: 50px;
      }
    }

    &-link{
      position: absolute;
      display: block;
      font-size: 20px;
      right: 40px;
      bottom: 60px;
      z-index: 20;

      a{
        color: #fff;
        text-decoration: none;
      }

      span{
        padding-right: 20px;
      }

      img{
        margin-bottom: -5px;
      }
    }

  }
}