.home-page .video {
  background: #050505;
  min-height: 100vh;
  padding: 30px;

  &-title {
    margin-bottom: 30px;
  }

  &-logo {
    border-radius: 10px;
    margin: 0 auto;
    max-width: 80%;
    min-height: 500px;
    box-shadow: inset 0px 0px 23px #2C2C2C;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  img {
    max-width: 60%;
  }

  @media only screen and (min-width: 1024px) {
    img {
      max-width: 100%;
    }
  }

}