.home-page .service {
  background: url("../../../assets/images/service-bg-mob.png"), #050505;
  background-size: 100% 100%;
  font-family: 'FuturaBookC', sans-serif;
  color: #fff;
  font-size: 27px;

  &-inner {
    min-height: 100vh;
  }

  &-item {
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 18px;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 75px;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: linear-gradient(90deg, rgba(123, 123, 123, 0) 0%, rgba(76, 76, 76, 0.39) 50.31%, rgba(120, 120, 120, 0) 100%);
    }

    img {
      max-width: 140px;
    }

    span {
      display: inline-block;
      max-width: 150px;
    }
  }

  &-list {
    display: none;
    position: relative;
    min-height: 100vh;
  }

  @media only screen and (min-width: 768px) {
    background: url("../../../assets/images/service-bg.png"), #050505;
    background-size: cover;

    &-inner {
      display: none;
    }

    &-list {
      display: block;
      height: 100%;
      font-size: 27px;

      &-photo {
        position: absolute;
        right: 10%;
        bottom: 70px;

        img {
          max-width: 80%;
        }
      }

      &-item {
        position: absolute;
        text-align: center;

        span {
          display: inline-block;
          max-width: 150px;
        }

        &:hover{
          text-shadow: 0 0 15px #fff;
        }

      }

      &-item:nth-child(1) {
        top: 15%;
        right: 5%;
      }

      &-item:nth-child(2) {
        top: 20%;
        right: 30%;
      }

      &-item:nth-child(3) {
        top: 15%;
        right: 52%;
      }

      &-item:nth-child(4) {
        top: 30%;
        left: 17%;
      }

      &-item:nth-child(5) {
        top: 52%;
        left: 12%;
      }

      &-item:nth-child(6) {
        top: 70%;
        left: 2%;
      }

    }
  }

  @media only screen and (min-width: 1024px) {

    &-list {
      &-photo {
        right: 25%;

        img {
          max-width: 100%;
        }
      }
    }
  }

}