.home-page .team {
  min-height: 100vh;
  padding: 30px 0;
  position: relative;
  font-family: 'FuturaBookC', sans-serif;

  &-title {
    margin-bottom: 30px;
  }

  &-members {

    &-item {
      max-width: 330px;
      padding: 0 25px;
      color: #fff;
      margin-bottom: 20px;

      img {
        max-width: 100%;
      }
    }

    &-name {
      padding: 23px;
      font-size: 20px;
      letter-spacing: 7px;
    }

    &-work {
      text-transform: uppercase;
      font-weight: 500;
      color: #909090;
      font-size: 25px;
      margin-bottom: 20px;
      letter-spacing: 15px;

      &_active {
        color: #fff;
        font-size: 36px;
        text-shadow: 0 0 10px #fff;
      }
    }

    &-info {
      border: 4.09241px solid #0038FF;
      border-radius: 8px;
      text-align: center;
    }

    &-position {
      margin-top: 25px;
      padding: 25px 20px 40px 20px;
      background: linear-gradient(123.13deg, rgba(119, 119, 119, 0.08) 15.98%, rgba(0, 10, 255, 0.34) 56.84%, rgba(124, 124, 124, 0.2) 83.86%);
    }

  }

  @media only screen and (min-width: 768px) {

    &-members-info {
      position: absolute;
      min-width: 250px;
      left: 50%;
      transform: translateX(-50%);
      bottom: 5%;
    }

    &-members-work {
      position: absolute;

      &_active {
        font-size: 28px;
      }
    }

    &-members-item:nth-child(1) .team-members-work {
      top: 30%;
      right: 7%;
    }

    &-members-item:nth-child(2) .team-members-work {
      top: 55%;
      left: 1%;
    }

    &-members-item:nth-child(3) .team-members-work {
      bottom: 10%;
      right: 7%;
    }

    &-members-item:nth-child(4) .team-members-work {
      top: 30%;
      left: 7%;
    }

    &-members-item:nth-child(5) .team-members-work {
      top: 55%;
      right: 1%;
    }

    &-members-item:nth-child(6) .team-members-work {
      bottom: 10%;
      left: 7%;
    }

  }

  @media only screen and (min-width: 1024px) {
    background: url("../../../assets/images/team-bg.png");
    background-size: cover;
    background-position: center;
  }


  @media only screen and (min-width: 1200px) {

    &-members-info {
      position: absolute;
      min-width: 330px;
      left: 50%;
      transform: translateX(-50%);
      bottom: 10%;
    }

    &-members-work {
      font-size: 36px;

      &_active {
        font-size: 36px;
      }
    }

    &-members-item:nth-child(1) .team-members-work {
      right: 15%;
    }

    &-members-item:nth-child(2) .team-members-work {
      left: 5%;
    }

    &-members-item:nth-child(3) .team-members-work {
      right: 15%;
    }

    &-members-item:nth-child(4) .team-members-work {
      left: 15%;
    }

    &-members-item:nth-child(5) .team-members-work {
      right: 5%;
    }

    &-members-item:nth-child(6) .team-members-work {
      left: 15%;
    }

  }


}