.footer {
  background: #050505;
  color: #fff;
  padding: 20px 0;
  font-family: 'FuturaMediumC', sans-serif;

  a, p {
    font-size: 12px;
    text-decoration: none;
    color: #fff;
  }

  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-links {

    &-logo {
      margin-bottom: 11px;

      img {
        max-width: 35px;
      }
    }

    &-contact {
      p {
        margin-bottom: 15px;
      }

      img {
        max-width: 12px;
        margin-right: 3px;
      }
    }

    &-social {
      list-style: none;
      display: flex;
      align-items: baseline;

      img {
        max-height: 15px;
      }
    }

    li {
      margin-right: 7px;
    }
  }

  &-contact {
    display: none;
  }

  &-nav {
    width: 55%;
    display: flex;
    justify-content: space-between;

    ul {
      list-style: none;
      margin-top: 15px;
    }

    li:nth-child(n+2) {
      margin-top: 13px;
    }
  }

  @media only screen and (min-width: 768px) {
    a, p {
      font-size: 16px;
    }

    &-links {
      &-logo {
        text-align: center;

        img {
          max-width: 55px;
        }
      }

      &-contact {
        display: none;
      }

      &-social img {
        max-height: auto;
      }

    }

    &-contact {
      display: block;

      p:nth-child(2) {
        margin-top: 35px;
      }

      img {
        max-width: 25px;
        margin-right: 3px;
        margin-bottom: -3px;
      }

    }

    &-nav {
      display: block;
      margin-bottom: -20px;

      ul {
        display: flex;
        justify-content: space-between;
        margin-top: 0;
      }

      ul:nth-child(n+2) {
        margin-top: 40px;
      }

      li {
        flex: 1;
      }

      li:nth-child(n+2) {
        margin-top: 0;
      }
    }

  }

  @media only screen and (min-width: 1200px) {
    a, p {
      font-size: 18px;
    }

    &-nav {
      margin-bottom: 0;
    }
  }

}