.menu {
  padding: 20px 0;
  //background: #050505;
  position: relative;
  font-family: 'FuturaMediumC', sans-serif;

  &-burger {
    position: relative;
    z-index: 10;
    color: #fff;

    &_des {
      display: none;
      color: #fff;
    }
  }

  &-list {
    position: absolute;
    z-index: 15;
    width: 100%;
    background: #212121;
    top: 0;
    text-align: center;
    padding: 60px 0 30px 0;
    transition: 0.3s all linear;
    right: 100%;

    li {
      margin-bottom: 20px;
    }

    a {
      text-decoration: none;
      color: #fff;
      font-size: 27px;
    }
  }

  &-open {
    right: 0;
  }

  &-close {
    position: absolute;
    right: 20px;
    top: 30px;
    color: #fff;
    font-size: 35px;

    &_des {
      color: #fff;
      font-size: 30px;
    }
  }

  @media only screen and (min-width: 1024px) {
    padding-top: 50px;

    &-inner{
      position: relative;
    }

    &-burger {
      display: none;

      &_des {
        display: inline-block;
        position: relative;
        z-index: 30;
      }

    }

    &-close{
      display: none;

      &_des{
        display: inline-block;
        position: relative;
        z-index: 30;
      }
    }

    &-list {
      width: auto;
      background: none;
      padding: 50px 0;
      opacity: 0;
      transition: opacity 0.5s linear;
      right: auto;
      left: 10%;
      top: -55px;

      ul {

      }

      li {
        display: inline-block;
        margin-right: 20px;
      }

    }

    &-open {
      left: 10%;
      opacity: 1;
    }
  }
}